body {
    background-color: #4C3B4D;
    color: #89B6A5;
}

#input-box {
    height: 100%;
    max-width: fit-content;
}

#preview-box {
    height: 100%;
}

blockquote {
    font-style: italic;
}

#preview {
    background-color: #CEC2FF;
    border: 1px solid black;
    width: 600px;
    height: 500px;
    max-width: fit-content;
    max-height: fit-content;
    overflow-y: auto;
    overflow-x: auto;
    color: #2B4570;
    box-shadow: 0.5rem 0.5rem 2px black;
}

#editor {
    width: 600px;
    height: 500px;
    background-color: #CEC2FF;
    color: #2B4570;
    box-shadow: 0.5rem 0.5rem 2px black;
}

#wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}